body {
  background-color: #fafafa;
  font-family: Montserrat;
}

button {
  cursor: pointer;
  transition-duration: .25s;
  transition-delay: 50ms;
}

button:hover {
  transform: scale(1.05);
}

::selection {
  background-color: #8ba0e6;
  transition-duration: .35s;
  transition-delay: 50ms;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 0;
}

p {
  margin: 0;
}

img {
  width: 100%;
  display: block;
}

input {
  border: none;
  outline: none;
}

textarea {
  resize: none;
  outline: none;
}

select {
  outline: none;
}

optgroup, option {
  margin: 0;
  padding: 0;
}

option:focus {
  background-color: #fff;
}

.box {
  flex-direction: column;
  align-items: center;
  display: flex;
  position: absolute;
  top: 200px;
  left: 300px;
}

[data-input] {
  width: 300px;
  height: 50px;
  border-radius: 15px;
  outline: 2.5px solid #3c4d5f;
  padding: 7.5px;
  font-weight: 500;
  transition-duration: .3s;
  transition-delay: 50ms;
}

[data-input]:focus {
  outline: none;
  box-shadow: 0 0 7px rgba(57, 79, 103, .83);
}

.tips {
  width: 250px;
  margin-top: 20px;
}

.tips__item {
  width: 100%;
  height: 60px;
  color: #eee;
  background-color: #394a5c;
  border-radius: 10px;
  align-items: center;
  margin-bottom: 15px;
  padding: 10px;
  display: flex;
}

.tips__item:last-child {
  margin-bottom: 0;
}

.card {
  width: 400px;
  height: 300px;
  color: #eee;
  background-color: #394a5c;
  border-radius: 10px;
  padding-top: 170px;
  padding-left: 10px;
  position: absolute;
  top: 200px;
  right: 300px;
  overflow: hidden;
}

.card__title {
  margin-bottom: 10px;
  font-size: 28px;
}

.card__text {
  margin-bottom: 5px;
}

.card__img {
  height: 150px;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
}

